
import logo from '../../assets/logo.png'
import React from 'react';
import { Header, Box as Bx, Text, TextInput, Button as Btn, Card, DataTable, Meter, Button, Table, TableHeader, TableBody, TableRow, TableCell } from 'grommet';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Bt from '@mui/material/Button';
import api from '../../services/api'
import { red } from '@mui/material/colors';
const Login = () => {

    const [logado, setLogado] = React.useState(false);
    const [orcando, setOrcando] = React.useState(false);
    const [user, setUser] = React.useState([]);

    const [unome, setUnome] = React.useState('');
    const [usobrenome, setUsobrenome] = React.useState('');
    const [utelefone, setUtelefone] = React.useState('');
    const [uemail, setUemail] = React.useState('');
    const [umarca, setUmarca] = React.useState('');
    const [umodelo, setUmodelo] = React.useState('');
    const [uano, setUano] = React.useState('');

    const [umartelinho, setUmartelinho] = React.useState(0);
    const [upintura, setUpintura] = React.useState(0);
    const [upolimento, setUpolimento] = React.useState(0);
    const [uvitrificacao, setUvitrificacao] = React.useState(0);
    const [urodas, setUrodas] = React.useState(0);
    const [uhigienizacao, setUhigienizacao] = React.useState(0);
    const [uoutros, setUoutros] = React.useState(0);

    const [total, setTotal] = React.useState(0);

    const handleSendOrcament = () => {

        var datatime = new Date();
        var dia = String(datatime.getDate()).padStart(2, '0');
        var mes = String(datatime.getMonth() + 1).padStart(2, '0');
        var ano = String(datatime.getFullYear());
        var fecha = dia + '/' + mes + '/' + ano;

        let data = {
            nome: unome,
            sobrenome: usobrenome,
            telefone: utelefone,
            email: uemail,
            marca: umarca,
            modelo: umodelo,
            ano: uano,
            valor_martelinho: parseInt(umartelinho),
            valor_pintura: parseInt(upintura),
            valor_polimento: parseInt(upolimento),
            valor_vitrificacao: parseInt(uvitrificacao),
            valor_rodas: parseInt(urodas),
            valor_higienizacao: parseInt(uhigienizacao),
            valor_outros: parseInt(uoutros),
            valor_total: total,
            data: fecha,
            id_funcionario: user.id,
            nome_funcionario: user.nome,
        }
        api.post('/enviar-orcamento', data).then((res) => {
            console.log(res.data)
        })
    }

    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' }
        // cardsServices: { }
    }
    const handleTotal = () => {

        let soma = parseInt(umartelinho) + parseInt(upintura) + parseInt(upolimento) + parseInt(uvitrificacao) + parseInt(urodas) + parseInt(uhigienizacao) + parseInt(uoutros);

        setTotal(soma);

    }
    const handleLogin = () => {
        let emailinput = document.querySelector("#email")
        let passwordinput = document.querySelector("#password")
        let data = {
            email: emailinput.value,
            password: passwordinput.value
        }
        api.post('/login', data).then((res) => {

            localStorage.setItem('token', JSON.stringify(res.data.access_token));

            api.defaults.headers.Authorization = `Bearer ${res.data.access_token}`;

            api.post('/me').then((res) => {
                setUser(res.data.usuario.original);
                setLogado(true);
            })

        })
        // setLogado(true);

    }

    React.useEffect(() => {

        const token = localStorage.getItem('token');
        if (token) {
            api.defaults.headers.Authorization = `Bearer ${JSON.parse(token)}`;

            api.post('/me').then((res) => {
                if (res.data.message === 'sucess') {
                    setUser(res.data.usuario.original);
                    setLogado(true);
                }
            })
        }
    }, [])


    return (
        <>
            {logado == false ? (
                <>
                    <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                        <img src={logo} style={{ height: '10vh', alignSelf: 'center' }} />


                    </Header><Card margin={{
                        left: '30%',
                        right: '30%',
                        top: 'xlarge'
                    }}
                        background={{ color: 'rgb(179 179 179)' }}
                        pad='large'
                    >
                        <Bx gap='medium'
                            margin={{
                                left: '20%',
                                right: '20%',

                            }}
                        >
                            <Stack
                                justifyContent="center"
                                alignItems="center"
                            >

                                <img src={logo} />
                            </Stack>
                            <TextField id="email" label="Email" variant="filled" sx={{ backgroundColor: 'white' }} />

                            <TextField id="password" label="Senha" variant="filled" sx={{ backgroundColor: 'white' }} type={'password'} />

                            <Btn primary label="Entrar" style={{ backgroundColor: '#F3C266', border: 'none' }} color='black' onClick={() => handleLogin()} />
                        </Bx>
                    </Card>
                </>) : (
                <>
                    <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                        <img src={logo} style={{ height: '10vh', alignSelf: 'center' }} />

                        <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>

                            <Bt variant="contained" sx={style.navbarAtual} onClick={() => setOrcando(true)} >Novo Orçamento</Bt>

                            <Bt variant="contained" sx={style.navbarAtual} >Sair</Bt>

                        </Stack>
                    </Header>
                    {orcando == false ? (<DataTable
                        columns={[
                            {
                                property: 'id_cotacao',
                                header: <Text>N° Cotacao</Text>,
                                primary: true,
                            },

                            {
                                property: 'data',
                                header: <Text>Data/Hora</Text>,
                                primary: true,
                            },
                            {
                                property: 'nome',
                                header: <Text>Nome</Text>,
                                primary: true,
                            },
                            {
                                property: 'sobrenome',
                                header: <Text>Sobrenome</Text>,
                                primary: true,
                            },
                            {
                                property: 'telefone',
                                header: <Text>Telefone</Text>,
                                primary: true,
                            },
                            {
                                property: 'email',
                                header: <Text>Email</Text>,
                                primary: true,
                            },
                            {
                                property: 'marca',
                                header: <Text>Marca</Text>,
                                primary: true,
                            },
                            {
                                property: 'modelo',
                                header: <Text>Modelo</Text>,
                                primary: true,
                            },
                            {
                                property: 'ano',
                                header: <Text>Ano</Text>,
                                primary: true,
                            },
                            {
                                property: 'foto1',
                                header: <Text truncate='true' >Foto 1:</Text>,
                                render: datum => (
                                    <a href="http://autovenice.com.br/wp-content/uploads/2011/03/toyota-corolla-2012-0021.jpg" target="_blank">Foto </a>
                                ),
                            },
                            {
                                property: 'foto2',
                                header: <Text truncate='true'>Foto 2:</Text>,
                                render: datum => (
                                    <img src={datum.foto2} />
                                ),
                            },
                            {
                                property: 'foto3',
                                header: <Text truncate='true'>Foto 3:</Text>,
                                render: datum => (
                                    <img src={datum.foto3} />
                                ),
                            },
                            {
                                property: 'foto4',
                                header: <Text truncate='true'>Foto 4:</Text>,
                                render: datum => (
                                    <img src={datum.foto4} />
                                ),
                            },
                            {
                                property: 'obs',
                                header: <Text>Observações</Text>,
                                primary: true,
                            },


                            {
                                property: 'percent',
                                header: <Text>Opcoes</Text>,
                                render: datum => (
                                    <Bx pad={{ vertical: 'xsmall' }}>
                                        <Button label='Fazer orçamento' style={{ backgroundColor: '#00ed1b', border: 'none' }} onClick={() => { }} />
                                    </Bx>
                                ),
                            },
                        ]}
                        data={[
                            { id_cotacao: '1/2022', data: '01/04/2022', nome: 'Esdras', sobrenome: 'Henrique', telefone: '33999342095', email: 'ehop100@gmail.com', marca: 'Toyota', modelo: 'Corolla', ano: '2010', foto1: 'teste', foto2: null, foto3: null, foto4: null, obs: 'Parachoque quebrado', percent: 20 },
                            { id_cotacao: '1/2022', data: '01/04/2022', nome: 'Esdras', sobrenome: 'Henrique', telefone: '33999342095', email: 'ehop100@gmail.com', marca: 'Toyota', modelo: 'Corolla', ano: '2010', foto1: 'teste', foto2: null, foto3: null, foto4: null, obs: 'Parachoque quebrado', percent: 20 },
                            { id_cotacao: '1/2022', data: '01/04/2022', nome: 'Esdras', sobrenome: 'Henrique', telefone: '33999342095', email: 'ehop100@gmail.com', marca: 'Toyota', modelo: 'Corolla', ano: '2010', foto1: 'teste', foto2: null, foto3: null, foto4: null, obs: 'Parachoque quebrado', percent: 20 },
                            { id_cotacao: '1/2022', data: '01/04/2022', nome: 'Esdras', sobrenome: 'Henrique', telefone: '33999342095', email: 'ehop100@gmail.com', marca: 'Toyota', modelo: 'Corolla', ano: '2010', foto1: 'teste', foto2: null, foto3: null, foto4: null, obs: 'Parachoque quebrado', percent: 20 },

                        ]}
                    />) : (<>
                        <Stack justifyContent="center"
                            alignItems="center">
                            {/* <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}> */}

                            <h1>ORÇAMENTO</h1>


                            <Bx style={{ width: '30%' }} gap='small'>
                                <Text> Dados do cliente</Text>
                                <Bx direction='row' style={{ width: '100%' }} gap='small'>
                                    <TextInput
                                        value={unome}
                                        onChange={event => setUnome(event.target.value)}
                                        placeholder="Nome"
                                    // value={value}
                                    // onChange={event => setValue(event.target.value)}
                                    />
                                    <TextInput
                                        value={usobrenome}
                                        placeholder="Sobrenome"
                                        onChange={event => setUsobrenome(event.target.value)}
                                    // value={value}
                                    // onChange={event => setValue(event.target.value)}
                                    />
                                </Bx>
                                <TextInput
                                    value={utelefone}
                                    placeholder="Telefone: +55 (ddd)"
                                    onChange={event => setUtelefone(event.target.value)}
                                // value={value}
                                // onChange={event => setValue(event.target.value)}
                                />

                                <TextInput
                                    value={uemail}
                                    placeholder="Email"
                                    onChange={event => setUemail(event.target.value)}
                                // value={value}
                                // onChange={event => setValue(event.target.value)}
                                />

                                <Text> Dados do carro</Text>
                                <TextInput
                                    value={umarca}
                                    placeholder="Marca"
                                    onChange={event => setUmarca(event.target.value)}
                                // value={value}
                                // onChange={event => setValue(event.target.value)}
                                />
                                <TextInput
                                    value={umodelo}
                                    placeholder="Modelo"
                                    onChange={event => setUmodelo(event.target.value)}
                                // value={value}
                                // onChange={event => setValue(event.target.value)}
                                />
                                <TextInput
                                    value={uano}
                                    placeholder="Ano"
                                    onChange={event => setUano(event.target.value)}
                                // value={value}
                                // onChange={event => setValue(event.target.value)}
                                />
                                <Text> O que fazer</Text>
                                <Table>
                                    <TableBody>
                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Martelinho</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}><Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                alignItems="center" >
                                                R$  <TextInput
                                                    value={umartelinho}
                                                    type={'number'}

                                                    // value={value}
                                                    onChange={event => { setUmartelinho(event.target.value); }}
                                                    onBlur={() => handleTotal()}
                                                />
                                            </Stack></TableCell>
                                        </TableRow>

                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Pintura</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={upintura}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUpintura(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Polimento</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={upolimento}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUpolimento(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Vitrificação</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={uvitrificacao}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUvitrificacao(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Pintura de rodas</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={urodas}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUrodas(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>
                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Higienização</Text>
                                            </TableCell>
                                            <TableCell width={'100%'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={uhigienizacao}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUhigienizacao(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>

                                        <TableRow>
                                            <TableCell scope="row">
                                                <Text>Outros</Text>
                                            </TableCell>
                                            <TableCell width={'18vw'}>
                                                <Stack direction={'row'} justifyContent="left" gap={'1%'}
                                                    alignItems="center" >
                                                    R$  <TextInput
                                                        value={uoutros}
                                                        type={'number'}

                                                        // value={value}
                                                        onChange={event => { setUoutros(event.target.value); }}
                                                        onBlur={() => handleTotal()}
                                                    />
                                                </Stack>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                                <Stack direction={'row'} justifyContent="center" gap={'20%'}
                                    alignItems="center"  >
                                    <Text>Total</Text>
                                    <Stack direction={'row'} justifyContent="center" gap={'1%'}
                                        alignItems="center" >
                                        R$  <TextInput
                                            type={'number'}
                                            value={total}

                                        // value={value}
                                        // onChange={event => setValue(event.target.value)}
                                        />
                                        <Btn primary label="Calcular" style={{ backgroundColor: '#F3C266', border: 'none' }} onClick={() => handleTotal()} color='black' />
                                    </Stack>
                                </Stack>

                                <Btn primary label="Enviar" style={{ backgroundColor: '#F3C266', border: 'none' }} onClick={() => handleSendOrcament()} color='black' />
                                <Btn primary label="Cancelar" style={{ backgroundColor: '#B3B3B3', border: 'none', marginBottom: '5%' }} color='black' onClick={() => setOrcando(false)} />
                            </Bx>
                            {/* </Box> */}
                        </Stack>
                    </>)}




                </>
            )
            }


        </>
    );
}

export default Login;
