import Button from '@mui/material/Button';
import logo from '../../assets/logo.png'
import { Box } from '@mui/system';
import Stack from '@mui/material/Stack';
import img2 from '../../assets/imgs/304.png'
import img3 from '../../assets/imgs/305.png'
import car from '../../assets/imgs/carro.png'
import backgroundSemiFooter from '../../assets/imgs/backgroundHome.jpg'
import img1SemiFooter from '../../assets/imgs/autoserv1.jpg'
import img2SemiFooter from '../../assets/imgs/autoserv2.jpg'
import img3SemiFooter from '../../assets/imgs/autoserv3.jpg'
import img4SemiFooter from '../../assets/imgs/autoserv4.jpg'
import { Header, Menu, Card, CardHeader, CardBody, Box as Bx, Text, TextInput, FileInput, TextArea, Button as Btn } from 'grommet';
import { Link } from "react-router-dom";
import Polirimg from '../../assets/imgs/polimg.jpg'
import martelinhoimg from '../../assets/imgs/martelinhoservices.jpg'
import funilariaimg from '../../assets/imgs/funilariaimg.jpg'
import higenizacaoimg from '../../assets/imgs/higenizacaoimg.jpg'
import vidroimg from '../../assets/imgs/vidroimg.jpg'
import rodaimg from '../../assets/imgs/rodaimg.jpg'
import api from '../../services/api';
import React from 'react'
const Orcamento = () => {
    const middleButton = { textAlign: 'center', color: 'white', fontSize: 20 }
    const footerButton1 = { textAlign: 'center', color: 'white', fontSize: 40 }
    const footerButton2 = { textAlign: 'center', color: 'white', fontSize: 20 }
    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30 },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30 }
        // cardsServices: { }
    }

    const [unome, setUnome] = React.useState('');
    const [usobrenome, setUsobrenome] = React.useState('');
    const [utelefone, setUtelefone] = React.useState('');
    const [uemail, setUemail] = React.useState('');
    const [umarca, setUmarca] = React.useState('');
    const [umodelo, setUmodelo] = React.useState('');
    const [uano, setUano] = React.useState('');

    const [foto1, setFoto1] = React.useState(null);
    const [foto2, setFoto2] = React.useState(null);
    const [foto3, setFoto3] = React.useState(null);
    const [foto4, setFoto4] = React.useState(null);

    const [obs, setObs] = React.useState('');

    const handleInfos = () => {
        console.log(foto1);
        let data = {
            nome: unome,
            sobrenome: usobrenome,
            telefone: utelefone,
            email: uemail,
            marca: umarca,
            modelo: umodelo,
            ano: uano,
            foto1: foto1,
            foto2: foto2,
            foto3: foto3,
            foto4: foto4,
            obs: obs,
        }

        api.post('enviar-informacoes', data).then((res) => {
            console.log(res.data)
        })
    }
    return (
        <>
            <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                <img src={logo} style={{ height: '10vh' }} />

                <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>
                    <Link to="/" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Home</Button>
                    </Link>
                    <Link to="/sobrenos" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Sobre nós</Button>
                    </Link>
                    <Link to="/servicos" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Serviços</Button>
                    </Link>
                    <Link to="/contato" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Contato</Button>
                    </Link>
                    <Button variant="contained" sx={style.navbarAtual}>Fazer Orçamento</Button>
                </Stack>
            </Header>



            <Bx
                direction="column"
                border={
                    [
                        {
                            color: "transparent",
                            size: "30vh",
                            style: "solid",
                            side: "left"
                        },
                    ]}
                pad="medium"
            >
                {/* <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}> */}

                <h1 style={{ marginLeft: '32%', fontFamily: 'PricedownBl', fontSize: 40}}>ORÇAMENTO</h1>


                <Bx style={{ marginLeft: '22%', width: '30%' }} gap='medium'>
                    <Bx direction='row' style={{ width: '100%' }} gap='small'>
                        <TextInput
                            value={unome}
                            onChange={event => setUnome(event.target.value)}
                            placeholder="Nome"
                        // value={value}
                        // onChange={event => setValue(event.target.value)}
                        />
                        <TextInput
                            value={usobrenome}
                            placeholder="Sobrenome"
                            onChange={event => setUsobrenome(event.target.value)}
                        // value={value}
                        // onChange={event => setValue(event.target.value)}
                        />
                    </Bx>
                    <TextInput
                        value={utelefone}
                        placeholder="Telefone: +55 (ddd)"
                        onChange={event => setUtelefone(event.target.value)}
                    // value={value}
                    // onChange={event => setValue(event.target.value)}
                    />

                    <TextInput
                        value={uemail}
                        placeholder="Email"
                        onChange={event => setUemail(event.target.value)}
                    // value={value}
                    // onChange={event => setValue(event.target.value)}
                    />
                    <TextInput
                        value={umarca}
                        placeholder="Marca"
                        onChange={event => setUmarca(event.target.value)}
                    // value={value}
                    // onChange={event => setValue(event.target.value)}
                    />
                    <TextInput
                        value={umodelo}
                        placeholder="Modelo"
                        onChange={event => setUmodelo(event.target.value)}
                    // value={value}
                    // onChange={event => setValue(event.target.value)}
                    />
                    <TextInput
                        value={uano}
                        placeholder="Ano"
                        onChange={event => setUano(event.target.value)}
                    // value={value}
                    // onChange={event => setValue(event.target.value)}
                    />



                    <FileInput
                        messages={{
                            browse: "Procurar",
                            dropPrompt: "Solte o arquivo aqui",
                            files: "files",
                            remove: "remover",
                        }}
                        name="foto-obrigatoria"

                        onChange={(event, { files }) => {
                            const fileList = files;
                            for (let i = 0; i < fileList.length; i += 1) {
                                const file = fileList[i];
                                setFoto1(file);
                                console.log(file);

                            }

                        }}
                    />

                    <FileInput
                        messages={{
                            browse: "Procurar",
                            dropPrompt: "Solte o arquivo aqui",
                            files: "files",
                            remove: "remover",
                        }}
                        name="foto-obrigatoria"
                        onChange={event => { setFoto2(event.target.file[0]); }}
                    />

                    <FileInput
                        messages={{
                            browse: "Procurar",
                            dropPrompt: "Solte o arquivo aqui",
                            files: "files",
                            remove: "remover",
                        }}
                        name="foto-obrigatoria"
                        onChange={event => { setFoto3(event.target.file[0]); }}
                    />

                    <FileInput
                        messages={{
                            browse: "Procurar",
                            dropPrompt: "Solte o arquivo aqui",
                            files: "files",
                            remove: "remover",
                        }}
                        name="foto-obrigatoria"
                        onChange={event => { setFoto4(event.target.file[0]); }}
                    />

                    <Text>Fale mais do que precisa para seu carro </Text>
                    <TextArea
                        placeholder="OBSERVAÇÕES"
                        value={obs}
                        onChange={event => setObs(event.target.value)}
                    />

                    <Btn primary label="Enviar" style={{ backgroundColor: '#F3C266', border: 'none' }} onClick={() => { handleInfos() }} color='black' />
                </Bx>
                {/* </Box> */}
            </Bx>


            {/* <Footer background={{ color: 'rgb(179 179 179)' }} pad="medium">
                <h2 style={{ textAlign: 'center', color: 'black' }}>Rua Professor Costa Nunes s/n-Barra da Tijuca
                    Altura do número 10.500 da Av das Américas
                    Entrada logo após o Hotel Américas (atrás da Localiza) : +55 21 99139-3332
                </h2>

            </Footer> */}







        </>
    );
}

export default Orcamento;
