import Button from '@mui/material/Button';
import logo from '../../assets/logo.png'
import { Box } from '@mui/system';
import Stack from '@mui/material/Stack';
import img2 from '../../assets/imgs/rodrigo.png'
import img3 from '../../assets/imgs/marcelo.png'
import car from '../../assets/imgs/carro.png'
import backgroundSemiFooter from '../../assets/imgs/backgroundHome.jpg'
import img1SemiFooter from '../../assets/imgs/autoserv1.jpg'
import img2SemiFooter from '../../assets/imgs/autoserv2.jpg'
import img3SemiFooter from '../../assets/imgs/autoserv3.jpg'
import img4SemiFooter from '../../assets/imgs/autoserv4.jpg'
import { Header, Menu, Card, CardHeader, CardBody, Box as Bx, Text, TextInput, FileInput, TextArea, Button as Btn, Footer } from 'grommet';
import { Link } from "react-router-dom";
import Polirimg from '../../assets/imgs/polimg.jpg'
import martelinhoimg from '../../assets/imgs/martelinhoservices.jpg'
import funilariaimg from '../../assets/imgs/funilariaimg.jpg'
import higenizacaoimg from '../../assets/imgs/higenizacaoimg.jpg'
import vidroimg from '../../assets/imgs/vidroimg.jpg'
import rodaimg from '../../assets/imgs/rodaimg.jpg'
const Sobrenos = () => {
    const middleButton = { textAlign: 'center', color: 'white', fontSize: 20 }
    const footerButton1 = { textAlign: 'center', color: 'white', fontSize: 40 }
    const footerButton2 = { textAlign: 'center', color: 'white', fontSize: 20 }
    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30 },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'PricedownBl', fontSize: 30 }
        // cardsServices: { }
    }
    return (
        <>
            <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                <img src={logo} style={{ height: '10vh' }} />

                <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>
                    <Link to="/" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Home</Button>
                    </Link>

                    <Button variant="contained" sx={style.navbarAtual}>Sobre nós</Button>

                    <Link to="/servicos" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Serviços</Button>
                    </Link>
                    <Link to="/contato" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Contato</Button>
                    </Link>
                    <Link to="/orcamento" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario} >Fazer Orçamento</Button>
                    </Link>
                </Stack>
            </Header>



            <Bx
                direction="row"
                border={[
                    {
                        color: "transparent",
                        size: "84vh",
                        side: "left"
                    },
                    {
                        color: "transparent",
                        size: "60vh",
                        side: "right"
                    },
                ]}
                pad="medium"
            >
                <h1 style={{ textAlign: 'center', fontFamily: 'PricedownBl', fontSize: 40 }}> Sobre nós</h1>
            </Bx>
            <Bx
                direction="row"
                border={[
                    {
                        color: "transparent",
                        size: "60vh",
                        side: "left"
                    },
                    {
                        color: "transparent",
                        size: "60vh",
                        side: "right"
                    },
                ]}
                pad="medium"
            >
                <Text textAlign='center'>A região da Barra e Recreio já tem o MAIS MODERNO E COMPLETO centro de estética automotiva do Rio de Janeiro. Instalado em um terreno de mais de 5000 m2 no coração da Barra da Tijuca oferecendo tudo que seu carro precisa para ficar ainda mais lindo! O MARTELINHO MEGA BLASTER chega para revolucionar o mercado !

                    O Martelinho MB está unindo a experiência de mais de 10 anos no mercado automotivo do Rio de Janeiro com a qualidade e tecnologia chilena !

                    Os sócios Marcelo Carlos e Rodrigo Durán trazem o melhor destes dois mundos para você.

                    Serviços de alta qualidade e  atendimento personalizado.</Text>
            </Bx>

            <Bx direction='row' background='rgb(179 179 179)' pad={'small'} style={{ justifyContent: 'center' }} gap='large'>
                <Card height="medium" width="medium" background="#9795A3">
                    <CardHeader pad="medium">  <img src={img2} style={{ height: 200, width: 'auto', marginLeft: '20%' }} /> </CardHeader>
                    <CardBody pad="medium"><h1 style={middleButton}> Rodrigo Durán </h1>

                    </CardBody>
                </Card>

                <Card height="medium" width="medium" background="#9795A3">
                    <CardHeader pad="medium"> <img src={img3} style={{ height: 200, width: 'auto', marginLeft: '20%' }} /> </CardHeader>
                    <CardBody pad="medium">
                        <h1 style={middleButton}> Marcelo Carlos </h1>
                    </CardBody>
                </Card>
            </Bx>
            <Footer background={{ color: 'rgb(179 179 179)' }} pad="medium" style={{ marginTop: '1.5%' }}>
                <Stack justifyContent="center"
                    alignItems="center">
                    <h2 style={{ color: 'black' }}>Rua Professor Costa Nunes s/n-Barra da Tijuca
                        Altura do número 10.500 da Av das Américas
                        Entrada logo após o Hotel Américas (atrás da Localiza) : +55 21 99139-3332
                    </h2>
                </Stack>
            </Footer>
        </>
    );
}

export default Sobrenos;
